<template>
  <div id="material-book">
    <div class="panel panel-default">
      <div class="panel-heading">
        <h4 class="panel-title">
          <small><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span></small>
          
        </h4>
      </div>
      <div class="panel-body">
        <button type="button" data-toggle="collapse"
          href="#edit" class="btn btn-sm btn-primary btn-group btn-group-justified">
          <small><span class="glyphicon glyphicon-plus"></span></small>
          
        </button>
        <div id="edit" class="collapse">
          <form role="form" @submit.prevent="materialCreate">
            <div class="form-group">
              <label for="name"></label>
              <input type="text" class="form-control" id="name" placeholder="" required v-model="name"></input>
            </div>
            <div class="form-group">
              <label for="quantity">10000</label>
              <input type="number" class="form-control" id="quantity" placeholder="" required v-model.number="quantity"></input>
            </div>
            <div class="form-group">
              <label for="unit"></label>
              <input type="text" class="form-control" id="unit" placeholder="" required v-model="unit"></input>
            </div>
            <button type="submit" class="btn btn-sm btn-primary btn-group btn-group-justified">
              <small><span class="glyphicon glyphicon-floppy-disk"></span></small> 
            </button>
          </form>
        </div>
      </div>
       <ul class="list-group">
        <li class="list-group-item" v-for="material of materials">
          <small><span class="glyphicon glyphicon-bookmark"></span></small>
          <label>{{ material.name }}</label>  <label>{{ material.quantity }}</label> {{ material.unit }} <label>{{ material.left < 0 ? 0 : material.left }}</label> {{ material.unit }}<br>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-primary dropdown-toggle" @click="materialListGet"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model.number="material.quantity" type="text" class="form-control input-sm">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-primary" type="button" @click="materialUpdateQuantity(material)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-danger dropdown-toggle"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model="ok" type="text" class="form-control input-sm" placeholder="bgs">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-danger" type="button" @click="materialRemove(material._id)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
        </li>
      </ul>
    </div>
    <div class="panel panel-default">
      <div class="panel-heading">
        <h4 class="panel-title">
          <small><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span></small>
          
        </h4>
      </div>
      <ul class="list-group">
        <li class="list-group-item" v-for="(materialBook, index) of materialBooks">
          <small><span class="glyphicon glyphicon-modal-window" aria-hidden="true"></span></small> {{ materialBook.user }}
          <small><span class="glyphicon glyphicon-user" aria-hidden="true"></span></small> {{ materialBook.name }}
          <small><span class="glyphicon glyphicon-phone" aria-hidden="true"></span></small> {{ materialBook.phone }}<br>
          <small><span class="glyphicon glyphicon-flag" aria-hidden="true"></span></small> {{ materialBook.activity }}<br>
          <small><span class="glyphicon glyphicon-calendar" aria-hidden="true"></span></small> {{ materialBook.takeDate }}<br>
          <small><span class="glyphicon glyphicon-calendar" aria-hidden="true"></span></small> {{ materialBook.returnDate }}<br>
          <div v-show="materialBook.remark"><small><span class="glyphicon glyphicon-comment" aria-hidden="true"></span></small> {{ materialBook.remark }}</div>
          <small><span class="glyphicon glyphicon-book" aria-hidden="true"></span></small>
          <label v-for="(book, index) of materialBook.book">
            ({{ index + 1 }})&nbsp{{ book.name }}{{ book.book }}{{ book.unit }}&nbsp&nbsp
          </label><br>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-danger dropdown-toggle"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model="ok" type="text" class="form-control input-sm" placeholder="bgs">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-danger" type="button" @click="materialBookRemove(materialBook._id)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
          <span class="label label-condition label-default" v-show="materialBook.condition === 'book'">
            
          </span>
          <span class="label label-condition label-primary" v-show="materialBook.condition === 'lend'">
            
          </span>
          <span class="label label-condition label-info" v-show="materialBook.condition === 'return'">
            
          </span>
          <span class="label label-condition label-danger" v-show="materialBook.condition === 'fail'">
            
          </span>
        </li>
        <li class="list-group-item" v-show="!materialBooks.length"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import api from '../api'
export default {
  data () {
    return {
      ok: '',
      name: '',
      unit: '',
      quantity: 1,
      materials: [],
      materialBooks: []
    }
  },
  methods: {
    materialCreate () {
      if (this.name && this.unit && this.quantity) {
        api.materialCreate({
          material: {
            name: this.name,
            unit: this.unit,
            quantity: this.quantity
          }
        }).then((res) => {
          if (res.status === 200) {
            this.materialListGet()
            this.materialBookListGetAll()
          } else {
            alert(res.data.msg)
          }
        }).catch((err) => {
          console.error(err)
          alert('')
        })
      }
    },
    materialUpdateQuantity (material) {
      api.materialUpdateQuantity({
        materialId: material._id,
        quantity: material.quantity
      }).then((res) => {
        if (res.status === 200) {
          this.materialListGet()
        } else {
          alert(res.data.msg)
        }
      }).catch((err) => {
        console.error(err)
        alert('')
      })
    },
    materialRemove (materialId) {
      if (this.ok === 'bgs') {
        api.materialRemove({ materialId })
          .then((res) => {
            if (res.status === 200) {
              this.materialListGet()
              this.materialBookListGetAll()
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
        this.ok = ''
      } else {
        alert('bgs')
      }
    },
    materialListGet () {
      api.materialListGet()
        .then((res) => {
          if (res.status === 200) {
            this.materials = res.data.materialList
          } else {
            alert(res.data.msg)
          }
        }).catch((err) => {
          console.error(err)
          alert('')
        })
    },
    materialBookRemove (materialBookId) {
      if (this.ok === 'bgs') {
        api.materialBookRemove({ materialBookId })
          .then((res) => {
            if (res.status === 200) {
              this.materialListGet()
              this.materialBookListGetAll()
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
        this.ok = ''
      } else {
        alert('bgs')
      }
    },
    materialBookListGetAll () {
      api.materialBookListGetAll()
        .then((res) => {
          if (res.status === 200) {
            this.materialBooks = res.data.materialBookList
          } else {
            alert(res.data.msg)
          }
        }).catch((err) => {
          console.error(err)
          alert('')
        })
    }
  },
  beforeMount () {
    this.materialListGet()
    this.materialBookListGetAll()
  }
}
</script>

<style scoped>
.edit-button {
  padding: 0px 5px;
}
.label-condition {
  padding: 8px;
}
#edit {
  margin-top: 15px;
}
</style>



