<template>
  <div id="user">
    <div class="panel panel-default">
      <div class="panel-heading">
        <h4 class="panel-title">
          <small><span class="glyphicon glyphicon-user" aria-hidden="true"></span></small>
          
        </h4>
      </div>
      <div class="panel-body">
        <button type="button" data-toggle="collapse"
          href="#edit" class="btn btn-sm btn-primary btn-group btn-group-justified">
          <small><span class="glyphicon glyphicon-edit"></span></small>
          
        </button>
        <div id="edit" class="collapse">
          <form role="form" @submit.prevent="adminUpdate">
            <div class="form-group">
              <label for="password"></label>
              <input type="password" class="form-control" id="password" placeholder="" required v-model="passwordForCheck"></input>
            </div>
            <div class="form-group">
              <label for="password"></label>
              <input type="password" class="form-control bottom-span" id="password" placeholder="" required v-model="newPassword"></input>
              <input type="password" class="form-control" id="password" placeholder="" required v-model="newPasswordForCheck"></input>
            </div>
            <button type="submit" class="btn btn-sm btn-primary btn-group btn-group-justified">
              <small><span class="glyphicon glyphicon-floppy-disk"></span></small> 
            </button>
          </form>
        </div>
      </div>
    </div>
    <div class="panel panel-default">
      <div class="panel-heading">
        <h4 class="panel-title">
          <small><span class="glyphicon glyphicon-modal-window" aria-hidden="true"></span></small>
          
        </h4>
      </div>
      <div class="panel-body">
        <button type="button" data-toggle="collapse"
          href="#new" class="btn btn-sm btn-primary btn-group btn-group-justified">
          <small><span class="glyphicon glyphicon-plus"></span></small>
          
        </button>
        <div id="new" class="collapse">
          <form role="form" @submit.prevent="userCreate">
            <div class="form-group">
              <label for="class"></label>
              <select class="form-control" v-model.number="isMain">
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
              </select>
            </div>
            <div class="form-group">
              <label for="department"></label>
              <input type="text" class="form-control" id="department" placeholder="" required v-model="department"></input>
            </div>
            <button type="submit" class="btn btn-sm btn-primary btn-group btn-group-justified">
              <small><span class="glyphicon glyphicon-floppy-disk"></span></small> 
            </button>
          </form>
        </div>
      </div>
      <ul class="list-group">
        <li class="list-group-item" v-for="(userItem, index) in users">
          <div class="bottom-span">
            <small><span class="glyphicon glyphicon-modal-window" aria-hidden="true"></span></small> {{ userItem.department }}
            <small v-show="userItem.reName"><span class="glyphicon glyphicon-user" aria-hidden="true"></span></small> {{ userItem.reName }}
            <small v-show="userItem.rePhone"><span class="glyphicon glyphicon-phone" aria-hidden="true"></span></small> {{ userItem.rePhone }}
          </div>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-primary dropdown-toggle"
                    :disabled="userItem.isAuth ? 'disabled' : null"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model="ok" type="text" class="form-control input-sm" placeholder="bgs">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-primary" type="button" @click="userAuth(userItem)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-info dropdown-toggle"
                    :disabled="!userItem.isAuth ? 'disabled' : null"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model="ok" type="text" class="form-control input-sm" placeholder="bgs">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-info" type="button" @click="userAuth(userItem)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-danger dropdown-toggle"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model="ok" type="text" class="form-control input-sm" placeholder="bgs">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-danger" type="button" @click="userRemove(userItem._id)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
          <div class="btn-group">
            <button type="button" class="btn btn-sm btn-default dropdown-toggle"
                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              
            </button>
            <ul class="dropdown-menu">
              <li class="edit-button">
                <div class="input-group">
                  <input v-model="ok" type="text" class="form-control input-sm" placeholder="bgs">
                  <span class="input-group-btn">
                    <button class="btn btn-sm btn-default" type="button" @click="userReset(userItem)"></button>
                  </span>
                </div>
              </li>
            </ul>
          </div>
          <span class="label label-condition"
            :class="{ 'label-primary': userItem.isAuth,
                      'label-info': !userItem.isAuth }">
            {{ userItem.isAuth ? '' : '' }}
          </span>
        </li>
        <li class="list-group-item" v-show="!users.length"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import api from '../api'
export default {
  data () {
    return {
      ok: '',
      adminId: JSON.parse(window.atob(this.$store.state.token.split('.')[1])).id,
      passwordForCheck: '',
      newPassword: '',
      newPasswordForCheck: '',
      department: '',
      isMain: '',
      users: []
    }
  },
  methods: {
    adminUpdate () {
      if (this.newPassword && this.newPasswordForCheck && this.passwordForCheck) {
        if (this.newPassword === this.newPasswordForCheck) {
          api.adminUpdate({
            adminId: this.adminId,
            newPassword: this.newPassword,
            passwordForCheck: this.passwordForCheck
          }).then((res) => {
            if (res.status === 200) {
              alert('')
              this.$store.dispatch('adminLogout')
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
        } else {
          alert('')
        }
      } else {
        alert('')
      }
    },
    userCreate () {
      if (this.department && this.isMain) {
        let isMainSend = (this.isMain !== 3)
        api.userCreate({ department: this.department, isMain: isMainSend })
          .then((res) => {
            if (res.status === 200) {
              this.department = ''
              this.userListGet()
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
      } else {
        alert('')
      }
    },
    userRemove (userId) {
      if (this.ok === 'bgs') {
        api.userRemove({ userId })
          .then((res) => {
            if (res.status === 200) {
              this.userListGet()
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
        this.ok = ''
      } else {
        alert('bgs')
      }
    },
    userAuth (user) {
      if (this.ok === 'bgs') {
        api.userAuth({ user })
          .then((res) => {
            if (res.status === 200) {
              this.userListGet()
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
        this.ok = ''
      } else {
        alert('bgs')
      }
    },
    userReset (user) {
      if (this.ok === 'bgs') {
        api.userReset({ user })
          .then((res) => {
            if (res.status === 200) {
              this.userListGet()
            } else {
              alert(res.data.msg)
            }
          }).catch((err) => {
            console.error(err)
            alert('')
          })
        this.ok = ''
      } else {
        alert('bgs')
      }
    },
    userListGet () {
      api.userListGet()
        .then((res) => {
          if (res.status === 200) {
            this.users = res.data.userList
          } else {
            this.users = []
          }
        }).catch((err) => {
          console.error(err)
          alert('')
        })
    }
  },
  beforeMount () {
    this.userListGet()
  }
}
</script>

<style scoped>
.edit-button {
  padding: 0px 5px;
}
.label-condition {
  padding: 8px;
}
.bottom-span {
  margin-bottom: 5px;
}
#edit, #new {
  margin-top: 15px;
}
</style>



